    color: #20272a; }
  #header .flag-select {
    position: absolute;
    top: 0;
    left: 10px;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none; }
    #header .flag-select .arrow-down {
      display: none;
      color: #fff; }

